import { Container } from "react-bootstrap";
import { DatabaseInitAndVerify } from '../DatabaseUtil';
import React, { lazy } from 'react'
import '../StatDependency'

//lazy load the character display
const CharacterDisplayCardPromise = import('../Character/CharacterDisplayCard');
const CharacterDisplayCard = lazy(() => CharacterDisplayCardPromise)
export default function TestDisplay(props) {
  DatabaseInitAndVerify()
  return <Container>
    <React.Suspense fallback={<span>Loading...</span>}>
      <CharacterDisplayCard editable characterId={"character_2"} />
    </React.Suspense>
  </Container>
}
